import React from 'react'

const Table = props => (
  <table>
    <thead>
      <tr>
        <th>Nombre</th>
        <th>Oficio</th>
        <th>Eliminar</th>
      </tr>
    </thead>
    <tbody>
      {props.users.length > 0 ? (
        props.users.map(user => (
          <tr key={user.id}>
            <td>{user.nombre}</td>
            <td>{user.oficio}</td>
            <td>
              
              <button class="buttonDelete"
                onClick={() => props.deleteUser(user.id)}
              
              >
                Delete
              </button>
            </td>
          </tr>
        ))
      ) : (
        <tr>
          <td colSpan={3}>Ya no hay Usuarios</td>
        </tr>
      )}
    </tbody>
  </table>
)

export default Table