import React, { useState } from 'react'

const Form = props => {
	const initialFormState = { id: null, Nombre: '', Oficio: '' }
	const [ user, setUser ] = useState(initialFormState)

	const handleInputChange = event => {
		const { name, value } = event.target

		setUser({ ...user, [name]: value })
	}

	return (
		<form
			onSubmit={event => {
				event.preventDefault()
				if (!user.Nombre || !user.oficio) return

				props.addUser(user)
				setUser(initialFormState)
			}}
		>
			<label>Nombre</label>
			<input type="text" name="name" value={user.nombre} onChange={handleInputChange} />
			<label>Oficio</label>
			<input type="text" name="username" value={user.oficio} onChange={handleInputChange} />
			<button class="buttonPrimary">Agregar</button>
		</form>
	)
}

export default Form